.dismount_countdown_desktop_tutorial {
  animation: dismount_countdown_desktop_tutorial .5s ease-in-out 1;
  background: rgba(30, 30, 30, 1);
  border: 2px solid grey;
  border-radius: 0px 0px 30px 30px;
  box-shadow: 0 10px 15px rgba(0, 0, 0, 1), 0px 0px 5px 5px rgba(0, 0, 0, 1) inset;
}

@keyframes dismount_countdown_desktop_tutorial
{
  50% {
    transform: translateX(2000px);
  }
}