.game_mobile_rank_portrait {
  background: rgb(30, 30, 30, 1);
  border-left: 2px solid grey;
  border-right: 2px solid grey;
  box-shadow: 0 15px 10px rgba(0, 0, 0, 0.75), 0px 0px 5px 2.5px rgba(0, 0, 0, 0.75) inset;
  padding: 10px;
  text-align: center;
}

.game_mobile_rank_portrait h1 {
  color: white;
  font-family: "Staatliches";
  font-size: calc(20px + (30 - 20) * ((100vw - 500px) / (1920 - 500)));
  font-weight: 300;
  margin: 0 auto 0 auto;
  padding: 0px 10px 0px 10px;
  text-align: center;
}

.game_mobile_rank_portrait h2 {
  animation: text_shadow_blink_yellow 1.75s ease-in-out infinite;
  background-color: #000;
  border: 1px solid grey;
  border-radius: 5px;
  box-shadow: 0px 0px 5px 2.5px rgba(0, 0, 0, 1), 0px 0px 2.5px 1px rgba(0, 0, 0, 1) inset, 0px 0px 5px 2.5px rgba(100, 100, 100, 0.75) inset;
  color: yellow;
  font-family: 'Conv_digital-7.mono';
  font-size: calc(23px + (33 - 23) * ((100vw - 500px) / (1920 - 500)));
  font-weight: 300;
  margin: 0 auto 0 auto;
  padding: 0px 10px 0px 10px;
  text-align: center;
  text-shadow: 0px 0px 2.5px yellow;
}
